const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const setHeads = {
  description: 'Sets parachain heads',
  isHidden: true,
  // inherent
  params: [param('heads', ['CandidateReceipt'])],
  type: []
};

module.exports = (name, index) => createSection(name, index)(createMethod => ({
  description: 'Parchains',
  public: {
    setHeads: createMethod('setHeads', 0)(setHeads)
  },
  private: {}
}));