const bnToBn = require('@polkadot/util/bn/toBn');

const extrinsicsRawRoot = require('./extrinsic/rootRaw');

module.exports = function header({
  digest: {
    logs = []
  } = {},
  extrinsicsRoot,
  number,
  parentHash = new Uint8Array(32),
  stateRoot = new Uint8Array(32)
}, extrinsics) {
  return {
    digest: {
      logs
    },
    extrinsicsRoot: extrinsics ? extrinsicsRawRoot(extrinsics) : extrinsicsRoot || extrinsicsRawRoot([]),
    number: bnToBn(number || 0),
    parentHash,
    stateRoot
  };
};