const uncheckedLength = require('@polkadot/extrinsics-codec/encode/length');

const trieRootOrdered = require('@polkadot/util-triehash/rootOrdered');

module.exports = function extrinsicsRootRaw(extrinsics = []) {
  return trieRootOrdered(extrinsics.map(uncheckedLength));
};