const rootRaw = require('@polkadot/primitives-builder/extrinsic/rootRaw');

const bnToU8a = require('@polkadot/util/bn/toU8a');

const u8aConcat = require('@polkadot/util/u8a/concat');

const u8aToU8a = require('@polkadot/util/u8a/toU8a');

const encodeArray = require('../encoder/array');

const encodeU8a = require('../encoder/u8a');

module.exports = function encodeHeader({
  digest: {
    logs
  },
  extrinsicsRoot,
  parentHash,
  number,
  stateRoot
}, extrinsics) {
  return u8aConcat(u8aToU8a(parentHash), bnToU8a(number, 64, true), u8aToU8a(stateRoot), extrinsics ? rootRaw(extrinsics) : u8aToU8a(extrinsicsRoot), encodeArray(logs.map(log => encodeU8a(u8aToU8a(log)))));
};