const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const activeParachains = {
  description: 'All registered parachains',
  key: 'para:chains',
  params: [],
  type: ['ParachainId']
};
const parachainCode = {
  description: 'Gets the code for a specific parachain',
  keys: 'para:code',
  params: [param('id', 'ParachainId')],
  type: 'Code'
};
const parachainHeads = {
  description: 'The heads of the parachains registered at present',
  key: 'para:head',
  params: [param('id', 'ParachainId')],
  type: 'Bytes'
};
const didUpdate = {
  description: 'Did the parachain heads get updated in this block',
  key: 'para:did',
  params: [],
  type: 'bool'
};

module.exports = name => createSection(name)(createMethod => ({
  description: 'Parachain related entries',
  public: {
    activeParachains: createMethod('activeParachains')(activeParachains),
    parachainCode: createMethod('parachainCode')(parachainCode),
    parachainHeads: createMethod('parachainHeads')(parachainHeads),
    didUpdate: createMethod('didUpdate')(didUpdate)
  }
}));