const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const set = {
  description: 'Set timestamp',
  isHidden: true,
  // inherent
  params: [param('timestamp', 'Timestamp')],
  type: []
};

module.exports = (name, index) => createSection(name, index)(createMethod => ({
  description: 'Timestamp',
  public: {
    set: createMethod('set', 0)(set)
  },
  private: {}
}));