const bnToU8a = require('@polkadot/util/bn/toU8a');

const u8aConcat = require('@polkadot/util/u8a/concat');

module.exports = function encodeLength(unchecked) {
  return u8aConcat(bnToU8a(unchecked.length, 32, true), unchecked);
};